// File: frontend/src/components/messages/MessageList.jsx

import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchMessages } from '../../actions/messageActions';
import '../messagelist/MessageList.css';

const MEDIA_BACKEND_URL = 'https://ppacilyoncentre.com/media/api/getMedia';

const getMessageViewType = (message) => {
  const hasImage = message.media?.some((m) => m.type === 'image');
  const hasVideo = message.media?.some((m) => m.type === 'video');

  let pressetype = '';

  if (!hasImage && !hasVideo) {
    pressetype = 'text-only';
  } else if (hasImage && !hasVideo) {
    pressetype = 'image-only';
  } else if (!hasImage && hasVideo) {
    pressetype = 'video-only';
  } else if (hasImage && hasVideo) {
    pressetype = 'image-and-video';
  }

  return pressetype;
};

const MessageList = () => {
  const dispatch = useDispatch();
  const messages = useSelector((state) => state.messages.messages);
  const [activeMessageId, setActiveMessageId] = useState(null);

  const toggleText = (id) => {
    setActiveMessageId((prevId) => (prevId === id ? null : id));
  };

  const isActive = (id) => activeMessageId === id;

  useEffect(() => {
    dispatch(fetchMessages());
  }, [dispatch]);

  useEffect(() => {
    if (!messages || messages.length === 0) return;

    const validMessages = Array.isArray(messages)
      ? messages.filter((msg) => msg && msg.id)
      : [];

    if (validMessages.length === 0) return;

    const fetchMediaForMessages = async () => {
      try {
        const enrichedMessages = await Promise.all(
          validMessages.map(async (message) => {
            const response = await fetch(`${MEDIA_BACKEND_URL}/${message.id}`, {
              headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`,
              },
            });
            if (!response.ok) {
              return { ...message, media: [] };
            }
            const mediaData = await response.json();
            return { ...message, media: mediaData || [] };
          })
        );

        dispatch({ type: 'UPDATE_MESSAGES', payload: enrichedMessages });
      } catch (error) {
        console.error('Erreur lors du fetch des mdias :', error);
      }
    };

    fetchMediaForMessages();
  }, [messages]);

  return (
    <div className="messagelist">
      <h2> Presse PPA-CI</h2>

      {!Array.isArray(messages) || messages === undefined ? (
        <p className="error-message"> Erreur : donnes des messages non disponibles.</p>
      ) : messages.length === 0 ? (
        <div className="no-message">
          <h3> Aucun message pour le moment</h3>
          <p>Connectez-vous pour publier le premier message.</p>
        </div>
      ) : (
        messages.map((message) => {
          const pressetype = getMessageViewType(message);

return (
  <div key={message.id} className={`message-card ${pressetype}`}>
    {pressetype === 'text-only' && (
      <>
        <h3 onClick={() => toggleText(message.id)}>{message.tittle}</h3>
        {isActive(message.id) && (
          <>
            <p>{message.content}</p>
            <p className="author">
              Expdi par : {message.User?.email || 'Utilisateur inconnu'}
            </p>
          </>
        )}
      </>
    )}

    {(pressetype === 'image-only' ||
      pressetype === 'video-only' ||
      pressetype === 'image-and-video') && (
      <>
        <div className="media-grid">
          {message.media?.map((file, index) => (
            <div key={file.filename} className="media-item">
              {index === 0 && <h3>{message.tittle}</h3>}
              {file.type === 'image' ? (
                <img
                  src={`https://ppacilyoncentre.com/media/api/uploads/images/${file.filename}`}
                  alt="Message media"
                />
              ) : (
                <video controls>
                  <source
                    src={`https://ppacilyoncentre.com/media/api/uploads/videos/${file.filename}`}
                    type="video/mp4"
                  />
                  Votre navigateur ne supporte pas la lecture des vidos.
                </video>
              )}
            </div>
          ))}
        </div>
        <p>{message.content}</p>
        <p className="author">
          Expdi par : {message.User?.email || 'Utilisateur inconnu'}
        </p>
      </>
    )}

    {/* Fallback si aucune vue ne sactive */}
    {pressetype !== 'text-only' &&
      pressetype !== 'image-only' &&
      pressetype !== 'video-only' &&
      pressetype !== 'image-and-video' && (
        <p> Type de message inconnu ou mal structur.</p>
    )}
  </div>
);

        })
      )}
    </div>
  );
};

export default MessageList;
